#toggle-editor label {
  margin-left: 2px;
  margin-bottom: 0;
}
#toggle-editor .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 24px;
}
#toggle-editor .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
#toggle-editor .switch input:checked + .slider {
  background-color: #408540;
}
#toggle-editor .switch input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
#toggle-editor .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #CCC;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}
#toggle-editor .slider::before {
  position: absolute;
  content: "";
  height: 16px;
  width: 25px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
  text-align: center;
  font-weight: bold;
}

